}

.prizes1:hover {
	background-color: #db4437;
	cursor: pointer;
	color: black;
	animation: fade-in-red 1s ease-out;
}

.prizes2 {
	color: white;
}

.prizes2:hover {
	background-color: #f4b400;
	cursor: pointer;
	color: black;
	animation: fade-in-yellow 0.7s ease-out;
}

.prizes3:hover {
	background-color: #0f9d58;
	cursor: pointer;
	color: black;
	animation: fade-in-green 0.7s ease-out;
}

.track-img {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	text-align: center;
}

.track0:hover .track-img {
	display: none;
}

.track1:hover .track-img {
	display: none;
}

.track2:hover .track-img {
	display: none;
}

.track3:hover .track-img {
	display: none;
}

