.track2:hover .track-info {
	display: block;
	text-align: center;
	animation: fade-in 0.7s ease-out;
}

.track3:hover .track-info {
	display: block;
	text-align: center;
	animation: fade-in 0.7s ease-out;
}

@keyframes fade-in {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes fade-in-red {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
		background-color: #db4437;
	}
}

@keyframes fade-in-yellow {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
		background-color: #f4b400;
	}
}

@keyframes fade-in-blue {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
		background-color: #4285f4;
	}
}

@keyframes fade-in-green {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
		background-color: #0f9d58;
	}
}
